import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({

  state: {
    nightMode: false
  },

  mutations: {
    togglenightMode(state) {
        state.nightMode = state.nightMode ? false : true;
        localStorage.setItem("nightMode", JSON.stringify(state.nightMode));
      }
  },

  actions: {
    togglenightMode({ commit }) {
      commit('togglenightMode')
    }
  },

  getters: {
    nightMode(state) {
      return state.nightMode
    }
  }
})