<template>
  <div id="app" class="app text-sm" v-bind:class="{ 'bg-darktheme text-white' : nightMode, 'bg-whitesmoke text-gray-700':!nightMode }">
    <ContentContainer />
    <Footer v-bind:class="{ 'bg-darktheme text-white' : nightMode, 'bg-whitesmoke text-gray-700':!nightMode }" />
  </div>
</template>
<script>
import ContentContainer from "./components/ContentContainer.vue";
import Footer from "./components/Footer.vue";
import AOS from "aos";
import "aos/dist/aos.css";
import "animate.css/animate.min.css";

export default {
  name: "App",
  components: {
    ContentContainer,
    Footer
  },
  mounted() {
    AOS.init({ duration: 1000 });
    if (localStorage.getItem("nightMode") == "true") {
      this.$store.dispatch("togglenightMode");
    } else if (localStorage.getItem("nightMode") == "false") {
      return;
    } else if (window.matchMedia("(prefers-color-scheme: dark)").matches) {
      this.$store.dispatch("togglenightMode");
    }
  },
  computed: {
    nightMode: function() {
      return this.$store.getters.nightMode;
    }
  }
};
</script>

<style>
@import "assets/styles/fonts.css";
:root {
  --animate-duration: 1s;
}
.app {
  font-family: "Cabin", sans-serif;
}
</style>
